import axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { cartDataCred } from "../redux/actions";
import "../product_overview/index.css";
export class Details extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ProductDetail: [],
      receivedProductId: props.match.params.pid,
    };
  }
  componentDidMount() {
    axios
      .get(
        "https://5d76bf96515d1a0014085cf9.mockapi.io/product/" +
          this.state.receivedProductId
      )
      .then((res) => this.setState({ ProductDetail: res.data }));
  }
  render() {
    return (
      <section className="mysection">
        <div className="leftDiv">
          <img src={this.state.ProductDetail.preview} className="mainP" />
        </div>
        <div className="rightDiv">
          <div>
            <h1>{this.state.ProductDetail.name}</h1>
            <h4>{this.state.ProductDetail.brand}</h4>
            <h3>
              <span>Price: Rs </span>
              <span>{this.state.ProductDetail.price}</span>
            </h3>
            <div>
              <h3>Description</h3>
              <p>{this.state.ProductDetail.description}</p>
            </div>
            <div>
              <h3>Product Preview</h3>
              <div className="SIcontainer">
                {this.state.ProductDetail.photos &&
                  this.state.ProductDetail.photos.map((a, index) => (
                    <SmallImg a={a} index={index} />
                  ))}
              </div>
            </div>
          </div>
          <button
            id="btn-add-to-cart"
            onClick={() => {
              this.props.SendingCartData(this.state.ProductDetail);
            }}
          >
            Add to Cart
          </button>
        </div>
      </section>
    );
  }
}
const mapDispatchToProps = (Dispatch) => ({
  SendingCartData: (payload) => Dispatch(cartDataCred(payload)),
});

export default connect(null, mapDispatchToProps)(Details);

export const SmallImg = (props) => {
  const handleBorder = (z, Sourc) => {
    let photos = document.getElementsByClassName("mainP");
    photos[0].setAttribute("src", Sourc);

    let AllPhotos = document.getElementsByClassName("borderImage");

    for (let i = 0; i < AllPhotos.length; i++)
      i === z
        ? AllPhotos[i].classList.add("borderBox")
        : AllPhotos[i].classList.remove("borderBox");
  };

  const { index, a } = props;

  return (
    <div
      id={index}
      className="borderImage"
      onClick={() => {
        handleBorder(index, a);
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      }}
    >
      <img src={a} className="smallImg"></img>
    </div>
  );
};
