import "../src/App.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import axios from "axios";
import { HashRouter, Route, Switch } from "react-router-dom";
import { productDataCred } from "./components/redux/actions";
import Details from "../src/components/product_overview";
import Topbar from "./components/topbar";
import Cart from "./components/cart";
import Checkout from "./components/checkout";
import Footer from "../src/components/footer";
import Carousel from "./components/corousel/index";
import { AccessoriesFiller, ClothFiller } from "./components/product_cards";
import Facility from "../src/components/extrafacility";

export class Home extends Component {
  componentDidMount() {
    axios
      .get("https://5d76bf96515d1a0014085cf9.mockapi.io/product")
      .then(res => this.props.SendingProductData(res.data));
  }
  render() {
    return (
      <HashRouter>
        <Topbar />
        <Switch>
          <Route path="/productOverview/:pid" component={Details}></Route>
          <Route path="/cart" component={Cart}></Route>
          <Route path="/checkout" component={Checkout}></Route>

          <div className="maindiv">
            <Carousel/>
            <Facility />
            <section className="ClothingSection" id="clothing">
              <h2>Clothing for Men and Women</h2>
              <div className="cardContainer1">
                {this.props.receiveProductData.length &&
                  this.props.receiveProductData[0].map(items => <ClothFiller {...items} />)
                  }
              </div>
            </section>

            <section className="AssessorySection" id="accessories">
              <h2>Accessories for Men and Women</h2>
              <div className="cardContainer1">
                {this.props.receiveProductData.length &&
                   this.props.receiveProductData[0].map(items => <AccessoriesFiller {...items} />)
                }
              </div>
            </section>
          </div>
        </Switch>
        <Footer />
      </HashRouter>
    );
  }
}

const mapStateToProps = (state) => ({
  receiveProductData: state.ProductDataArray
});

const mapDispatchToProps = (dispatch) => ({
  SendingProductData: (payload) => dispatch(productDataCred(payload))
});

export default connect(mapStateToProps, mapDispatchToProps)(Home);
