import React from 'react';
import "./index.css"
const Carousel = () => {
    return ( 
      <section className="carouselSection">
      <div id="carouselExampleIndicators" className = "carousel slide"  data-ride="carousel">
        <ol className="carousel-indicators">
          <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
          <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
          <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
          <li data-target="#carouselExampleIndicators" data-slide-to="3"></li>
        </ol>
        <div className="carousel-inner">
          <div className="carousel-item active">
            <img className="d-block myw w-100 " src="https://south-freckle-fiber.glitch.me/cimg1.png" alt="First slide"/>
          </div>
          <div className="carousel-item">
            <img className="d-block myw w-100" src="https://south-freckle-fiber.glitch.me/cimg2.png" alt="Second slide"/>
          </div>
          <div className="carousel-item">
            <img className="d-block myw w-100" src="https://south-freckle-fiber.glitch.me/cimg3.png" alt="Third slide"/>
          </div>
          <div className="carousel-item">
            <img className="d-block myw w-100" src="https://south-freckle-fiber.glitch.me/cimg4.png" alt="Fourth slide"/>
          </div>
        </div>
      </div>
    </section>
    );
}
 
export default Carousel;