import "../topbar/index.css"
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from "react-router-dom"

const burgerMenu = () => {
    document.getElementsByClassName('burger')[0].classList.toggle("toggle");
    document.getElementsByClassName('nav-links')[0].classList.toggle("nav-active");
}
const navigationHandler =()=>{
    document.getElementsByClassName('goBack')[0].classList.remove("nav-active");
    document.getElementsByClassName('goBackCross')[0].classList.remove("toggle")
}

export class Topbar extends Component {
    render() {
        return (
            <div className="top-bar">
                <div className="logo">
                    <a ><Link to={""}><h4><span>SHOP</span>LANE</h4></Link></a>
                </div>
                <ul className="nav-links goBack">
                    <li onClick={navigationHandler}><Link to={""}  style={{ textDecoration: 'none' }} >Home</Link></li>
                    <li onClick={navigationHandler}><a href="#clothing">Clothings</a></li>
                    <li onClick={navigationHandler}><a href="#accessories">Accessories</a></li>
                </ul>
                <div className="icons">
                    <i className="fa fa-search"></i>
                    <Link to="/cart" style={{ textDecoration: 'none' }}>
                        <div className="cart,cartIcon">
                            <a>
                                <i className="fa fa-shopping-cart">
                                    <sup className="cart-count">{this.props.cartCount.length}</sup>
                                </i>
                            </a>
                        </div>
                    </Link>
                    <i className="fa fa-user-circle-o"></i>
                </div>
                <div className="burger goBackCross" onClick={burgerMenu}>
                    <div className="line1"></div>
                    <div className="line2"></div>
                    <div className="line3"></div>
                </div>

            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    cartCount: state.CartDataArray
})


export default connect(mapStateToProps, null)(Topbar)