import React, { useState } from "react";
import { Link } from "react-router-dom";
import "../product_cards/index.css";
export const ClothFiller = ({
  id,
  preview,
  name,
  brand,
  isAccessory,
  price
}) => {
  const toProductOverview = "/productOverview" + "/" + id;
  return (
    <div className={!isAccessory ? "cc" : "empty"}>
      <Link to={toProductOverview} style={{ textDecoration: "none" }}>
        {!isAccessory && (
          <div className="card1" id={id}>
            <div className="imgContainer1">
              <img src={preview} />
            </div>
            <div className="detailDiv">
              <h3>{name}</h3>
              <h4>{brand}</h4>
              <h5>Rs. {price}</h5>
            </div>
          </div>
        )
        }
      </Link>
    </div>
  );
};

