const initialState = {
    ProductDataArray:[],
    CartDataArray:[]
    }
    
    function productData (state = initialState, { type, payload })  {
        switch (type) {
    
        case "shoplane":
            return { ...state, ProductDataArray:[...state.ProductDataArray,payload]}
        
        case "cart":
            return { ...state,CartDataArray:[...state.CartDataArray,payload]}

        case "emptyCart": {
                        state.CartDataArray.length=0;
                return {...state,CartDataArray:[...state.CartDataArray]}
        }
    
        default:
            return state
        }
    }
    export default productData
    